import { Avatar, CardHeader, Paper } from "@mui/material";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import { red } from "@mui/material/colors";
import Typography from "@mui/material/Typography";
import { doc, setDoc } from "firebase/firestore";
import React, { useCallback, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { debug } from "../constants";
import { db } from "../firebase/firebase";
import icon from "../img/ic_launcher_thinq.png";

const card = (props, message) => (
  <React.Fragment>
    <CardHeader
      avatar={
        <Avatar
          sx={{ width: 56, height: 56 }}
          aria-label="recipe"
          variant="rounded"
          src={icon}
        />
      }
      title={props.slider ? "ThinQ Gallery" : "ThinQ Talk"}
      titleTypographyProps={{ variant: "h5" }}
    />
    <CardContent>
      <Box sx={{ display: "flex" }}>
        <Avatar
          sx={{ bgcolor: red[300], width: 56, height: 56, mr: 2, my: "auto" }}
          aria-label="recipe"
        >
          
        </Avatar>
        <Typography variant="h5" component="div">
           
        </Typography>
      </Box>
      <Paper elevation={10} sx={{ my: 2, ml: 8, p: 6, borderRadius: 4 }}>
        <Typography variant="body2">{message}</Typography>
      </Paper>
      <Typography
        variant="subtitle1"
        color="text.secondary"
        sx={{ textAlign: "center" }}
      >
         "OK"   <br />
         
      </Typography>
    </CardContent>
  </React.Fragment>
);

function TextMessage(props) {
  const { image, slider, youtube } = props;
  const [event, setEvent] = useState("null");
  const navigate = useNavigate();

  let message = "     ~ ()";
  if (slider) {
    message = "   ~ ()";
  } else if (youtube) {
    message = "  ~ ()";
  }

  const keyListener = useCallback(
    (e) => {
      setEvent(e.key);
      if (e.key === "Enter") {
        if (image || slider) {
          navigate("/detail", {
            replace: true,
            state: { keyevent: e.key, message: message, slider },
          });
        } else if (youtube) {
          setDoc(doc(db, "video", "youtube"), {
            on: true,
          });
          setTimeout(() => navigate("/", { replace: true }), 500);
        }
        setDoc(doc(db, "messages", "type"), {
          type: "",
        });
      } else if (e.key === "GoBack") {
        // need finish web app
        navigate("/", { replace: true });
        setDoc(doc(db, "messages", "type"), {
          type: "",
        });
      }
    },
    [navigate, image, slider, youtube, message]
  );

  const clickListener = useCallback(
    (e) => {
      keyListener({ key: "Enter" });
    },
    [keyListener]
  );

  useEffect(() => {
    window.addEventListener("keydown", keyListener);
    window.addEventListener("click", clickListener);
    return () => {
      window.removeEventListener("keydown", keyListener);
      window.removeEventListener("click", clickListener);
    };
  }, [clickListener, keyListener]);
  return (
    <>
      {debug && (
        <Box sx={{ position: "absolute", top: 0, left: 4 }}>
          <Typography variant="h6" component="div" color="red">
            {event}
          </Typography>
        </Box>
      )}
      <Box
        sx={{
          position: "absolute",
          right: 10,
          bottom: 10,
        }}
      >
        <Card
          variant="outlined"
          sx={{
            backgroundColor: "#000000aa",
            width: 480,
            borderRadius: 20,
            p: 3,
          }}
        >
          {card(props, message)}
        </Card>
      </Box>
    </>
  );
}

export default TextMessage;
